.subsection{
	display:flex;
	justify-content: space-around;
	padding: 80px;
}

h2{
	font-size: 50px;
}
.headshot{
	width: 300px;
	height: 300px ;
	border-radius: 50%;
	object-fit: cover;
	object-position: 0 0;
	border-width: 3px;
	border: 6px solid darksalmon;
}
.headshotcontainer{
	display: flex;
	align-items: center;
}

@media only screen and (max-width: 1000px) {
	.headshotcontainer{
		display: none;
	}
}