h1{
    font-size: 90 px;
    margin: 0;
}

p{
    margin: 0;
    font-size: 30px; 
    font-weight: 300;
}

.navArea{
    display: flex;
    height: 100%;
    justify-content: space-between;
    align-items: center;
    margin:0;
    width: 100%;
}

.navText{
    margin-left: 80px;
    margin-bottom: 200px;
    width: 100%;
}

.button{
    width: 200px;
    height: 50px;
    border-radius: 25px;
    background-color: coral;
    text-align: center;
    font-size: 30px; 
    font-weight: 300; 
    padding-top:  5px;
    margin-top: 20px;
}
.button:hover{
    background-color: #ef744b;
}

.button:active{
    background-color: darkgrey;
    color: #808080;
}

.socials{
    padding-bottom: 200px;
}
@media only screen and (max-width: 1000px) {
     .button{ 
        padding:  5px 10px 0px 10px;
        width: 100px;
        height: 40px;
        font-size: 20px;
        font-weight: 300;
		transition: all 1s ease;
    }
}